.styled-table{
    border-collapse: collapse;
    margin: auto;
    font-size: 0.9rem;
    font-family: sans-serif;
    max-width: 100%;
    box-shadow: 0 0 23px rgba(0, 0, 0, 15);
   
    
}

.styled-table thead tr{
    background-color: dodgerblue;
    color: #ffffff;
    text-align: left;
}

.styled-table th,
.styled-table td{
    padding: 12px 15px;
    
}
.styled-table tbody tr{
    border-bottom: 1px solid #ddd;
    
}
.styled-table tbody tr:nth-of-type(even){
    background-color:#f3f3f3;
}
.styled-table tbody tr:last-of-type{
    border-bottom: 1px solid rgb(230, 230, 230);;
}

.btn{
    border: none;
    color: white;
    padding: 5px 8px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 5px;
    font-size: 15px;
    margin: 4px 2px;
    cursor: pointer;


}

.btn-edit{
    background-color: #e7e7e7e7;
    color: black;
    
}
.btn-edit:hover{
   background-color: dodgerblue;
   color: whitesmoke;
}

.btn.btn-delete{
    background-color: #e7e7e7e7;
    color: black;
    

}
.btn.btn-delete:hover{
    background-color: rgb(224, 36, 36);
    color: white;
    

}



.btn-view{
    background-color: #e7e7e7e7;
    color: black;
    
}
.btn-view:hover{
    background-color: rgb(212, 212, 212);
    color: rgb(15, 13, 13);
    }
.btn-add{
    background-color: #e7e7e7e7;
    color: black;
}

.btn-add:hover{

    background-color: rgb(25, 138, 44);
    color: white;
}

