.cls-btn{
  line-height: 15px;
  width: 35px;
  font-family: tahoma;
  margin-top: 20px;
  margin-right: 20px;
  position:absolute;
  top:0;
  right:0;
  border: none;
  color: rgb(0, 0, 0);
  padding: 5px 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;

}

.cls-btn:hover{
  background-color: rgba(226, 4, 4, 0.966);
  color: white;
}